@import './App.scss';

.pickedPlace {
    @include gradientBackgroundWorthOfSeeingPart2Portrait("../images/dominicaBackground.webp");
    @include backgroundDefaultSettings();
    margin-top: -.3vh;

    @include mobileLandscape {
        @include gradientBackgroundWorthOfSeeingPart2("../images/dominicaBackgroundLandscape.webp");
        @include backgroundDefaultSettings();
    }

    &--kuba {
        @include gradientBackgroundWorthOfSeeingPart2Portrait("../images/cubaBackground.webp");
        @include backgroundDefaultSettings();

        @include mobileLandscape {
            @include gradientBackgroundWorthOfSeeingPart2("../images/cubaBackgroundLandscape.webp");
            @include backgroundDefaultSettings();
        }
    }

    &--jamajka {
        @include gradientBackgroundWorthOfSeeingPart2Portrait("../images/jamaicaBackground2.webp");
        @include backgroundDefaultSettings();

        @include mobileLandscape {
            @include gradientBackgroundWorthOfSeeingPart2("../images/jamaicaBackgroundLandscape2.webp");
            @include backgroundDefaultSettings();
        }
    }

    &__content {
        @include flex-center(); //tutaj tez center dla footera
        flex-direction: column;
        padding: 15.141vh 0;

        @include mobileLandscape {
            padding: 7.227vw 0;
        }
    }

    &__main {
        @include flex-center();
        flex-direction: column;

        @include mobileLandscape {
            flex-direction: row-reverse;
            margin-bottom: 5.641vw;
        }
    }

    &__footer {
        width: 25.271vh;
        height: 13.61vh;

        @include mobileLandscape {
            width: 17.362vw;
            height: 9.351vw;
        }
    }

    &__map {
        width: inherit;
        height: inherit;
    }
}

/* ====================================================PNG IMG SUPPORT =========================================================*/
.no-webp .pickedPlace {
    @include gradientBackgroundWorthOfSeeingPart2Portrait("../images/dominicaBackground.png");
    @include backgroundDefaultSettings();

    @include mobileLandscape {
        @include gradientBackgroundWorthOfSeeingPart2("../images/dominicaBackgroundLandscape.png");
        @include backgroundDefaultSettings();
    }

    &--kuba {
        @include gradientBackgroundWorthOfSeeingPart2Portrait("../images/cubaBackground.png");
        @include backgroundDefaultSettings();

        @include mobileLandscape {
            @include gradientBackgroundWorthOfSeeingPart2("../images/cubaBackgroundLandscape.png");
            @include backgroundDefaultSettings();
        }
    }

    &--jamajka {
        @include gradientBackgroundWorthOfSeeingPart2Portrait("../images/jamaicaBackground2.png");
        @include backgroundDefaultSettings();

        @include mobileLandscape {
            @include gradientBackgroundWorthOfSeeingPart2("../images/jamaicaBackgroundLandscape2.png");
            @include backgroundDefaultSettings();
        }
    }

}

/* =========================================================================================================================*/

.galleryOfPickedPlace {
    position: relative;
    height: 31.868vh;
    width: 47.362vh;
    margin-bottom: 5.282vh;

    @include mobilePortrait {
        @include widthPortrait(26.9013, 64.715);
    }

    @include mobileLandscape {
        margin-bottom: 0;
        width: 32.364vw;
        height: 20.6383vw;
    }

    &__img {
        width: 100%;
        height: 100%;

        &--transition {
            @include animation(transitionImg .4s);
        }
    }

    &__counter {
        @include flex-center();
        position: absolute;
        bottom: 0%;
        right: 0%;
        color: white;
        background: rgba(0, 0, 0, 0.61);
        width: 5.496vh;
        height: 3.346vh;
        font-family: $fontMontserrat;
        font-weight: 400;
        font-size: 1.55282vh;
        line-height: 1.893vh;

        @include mobileLandscape {
            width: 4.263vw;
            height: 2.456vw;
            font-size: 1.1391vw;
            line-height: 1.3891vw;
        }
    }
}

.galleryButton {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: absolute;
    outline: none;
    border: none;
    background-color: #EBAC00;
    border-bottom-left-radius: 90px;
    border-top-left-radius: 90px;
    height: 8.275vh;
    width: 4.353vh;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;

    @include mobileLandscape {
        width: 3.662vw;
        height: 6.585vw;
    }

    &--right {
        right: -.1%;
    }

    &--left {
        transform: translateY(-50%) rotate(180deg);
    }

    &:active>&__arrow {
        transform: scale(1.1) rotate(180deg);
    }

    &__arrow {
        cursor: pointer;
        width: 2.641vh;
        height: 2.641vh;

        @include mobileLandscape {
            width: 2.126vw;
            height: 2.126vw;
        }

        &--left {
            transform: rotate(180deg);
        }

        &--right {
            transform: rotate(180deg);
        }
    }


}


.pickedPlaceInfo {
    display: flex;
    flex-direction: column;
    width: 47.362vh;
    margin-bottom: 5.282vh;

    @include mobilePortrait {
        @include widthPortrait(26.9013, 64.715);
    }


    @include mobileLandscape {
        margin-bottom: 0;
        margin-right: 6.1902vw;
        width: 37.726vw;
        height: auto;
    }

    &__informations {
        margin-bottom: 2.817vh;
        font-family: $fontMontserrat;
        font-style: normal;
        font-weight: 300;
        font-size: 2.289vh;
        line-height: 3.132vh;
        color: white;
        width: 47.008vh;

        @include mobilePortrait {
            @include widthPortrait(26.7, 64.231);
            @include fontLinePortrait(1.30015, 3.12769, 1.77898, 4.27956);
        }

        @include mobileLandscape {
            margin-bottom: 1.669vw;
            width: 37.725vw;
            height: auto;
            font-size: 1.2025vw;
            line-height: 2.5388vw;
        }
    }

    &__moreButton {
        cursor: pointer;
        width: 16.03vh;
        height: 4.402vh;
        font-family: $fontMontserrat;
        font-weight: 700;
        font-size: 2.289vh;
        line-height: 2.7905vh;
        border: none;
        color: white;
        background:
            linear-gradient(#EBAC00 0 0) top /100% var(--d, 100%) no-repeat,
            linear-gradient(#EBAC00 0 0) bottom /100% var(--d, 100%) no-repeat;
        transition: 0.5s;

        @include mobileLandscape {
            width: 9.712vw;
            height: 2.311vw;
            line-height: 1.46127vw;
            font-size: 1.2025vw;
        }

        &:hover {
            border: .354vh solid white;
            --d: 0%;

            @include mobileLandscape {
                border-width: .353vw;
            }
        }

        &:active {
            transform: scale(1.05);
            transition: 0s;
        }
    }
}

.pickedPlaceTitleContainer {
    @include flex-spaceBetweenCenter();
    flex-direction: row;

    margin-bottom: 2.4648vh;

    @include mobileLandscape {
        margin-bottom: 1.29402vw;
    }


    &__title {
        margin-left: .7043vh;
        font-family: $fontAbril;
        font-weight: 400;
        font-size: 5.6338vh;
        line-height: 7.6004vh;
        color: white;

        @include mobilePortrait {
            @include fontLinePortrait(3.2, 7.69802, 4.31703, 10.3852);
        }

        @include mobileLandscape {
            font-size: 2.958vw;
            line-height: 3.9912vw;
        }
    }
}

.pickedPlaceSliderControl {
    @include flex-center();
    flex-direction: row;

    @include mobileLandscape {
        margin-right: 1.53873vw;
    }

    &__buttonArrow {
        width: 2.606vh;
        height: 2.958vh;
        @include buttonReset();

        @include mobileLandscape {
            display: flex;
            width: 1.574vw;
            height: 1.725vw;
        }
    }

    &__arrow {
        width: inherit;
        height: inherit;

        cursor: pointer;

        &:active {
            transform: scale(1.1);
        }

        &--right {
            transform: rotate(180deg);

            &:active {
                transform: rotate(180deg) scale(1.1);
            }
        }
    }

    &__counter {
        margin: 0 1.03vh;
        width: 4.205vh;
        font-family: $fontMontserrat;
        font-weight: 400;
        font-size: 1.53vh;
        line-height: 1.8644vh;
        color: white;

        @include mobileLandscape {
            //36.2375px
            //width: 2.36vw;
            text-align: center;
            width: 3vw;
            margin: 0 .62148vw;
            font-size: .9243vw;
            line-height: 1.12676vw;
        }
    }
}